import React, {Component} from 'react'
// `WrappedComponent``wrappedComponentRef`

export default (WrappedComponent) => {
  return class withRef extends Component {
    static displayName = `withRef(${WrappedComponent.displayName || WrappedComponent.name || 'Component'})`;
    render() {
      // props:
      // this.props.refreact
      const props = {
        ...this.props,
      };
      // wrappedComponentRefref
      // `WrappedComponent`wrappedComponentRef`WrappedComponent`
      // [yangshenghaha]
      props.ref = (el)=>{
        this.props.wrappedComponentRef && this.props.wrappedComponentRef(el);this.props.ref && this.props.ref(el);
      }
      return (
        <WrappedComponent {...props} />
      );
    }
  };
};
